import React, { useMemo } from 'react';
import { ThemeProvider } from 'styled-components';
import { defaultTheme } from '../../../theme';
import {
  FormClassNameConfig,
  LayoutType,
  FormValues,
} from '../../dynamic-form/types';
import { useFormContext } from 'react-hook-form';
import {
  FormContainer,
  GridFormContainer,
  FormContainerProps,
  GridFormContainerProps,
} from '../../../styles';

// Create interface for common props
export interface LayoutComponentProps
  extends FormContainerProps,
    GridFormContainerProps {
  onSubmit: (event: React.FormEvent<HTMLFormElement>) => void;
  children: React.ReactNode;
  className?: string;
  formClassNameConfig?: FormClassNameConfig;
  style?: React.CSSProperties;
  $layout: LayoutType;
  layoutConfig?: any;
  horizontalLabel?: boolean;
}

interface FormLayoutProps {
  onSubmit: (event: React.FormEvent<HTMLFormElement>) => void;
  children: React.ReactNode;
  theme?: any;
  className?: string;
  formClassNameConfig?: FormClassNameConfig;
  style?: React.CSSProperties;
  layout: LayoutType;
  layoutConfig?: any;
  horizontalLabel?: boolean;
}

const FormLayout: React.FC<FormLayoutProps> = ({
  onSubmit,
  children,
  className,
  formClassNameConfig,
  style,
  layout,
  layoutConfig,
  horizontalLabel,
  theme,
}) => {
  const mergedTheme = useMemo(() => {
    return theme ? { ...defaultTheme, ...theme } : defaultTheme;
  }, [theme]);

  const { formState } = useFormContext<FormValues>();

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    onSubmit(event);
  };

  // Use GridFormContainer for grid layout, FormContainer for others
  const LayoutComponent: React.ComponentType<LayoutComponentProps> =
    layout === 'grid' ? GridFormContainer : FormContainer;

  const layoutComponentProps: LayoutComponentProps = {
    onSubmit: handleSubmit,
    className: `${className || ''} ${formClassNameConfig?.formContainer || ''}`,
    style,
    $layout: layout, // Pass layout type to styled component
    $layoutConfig: layoutConfig,
    $horizontalLabel: horizontalLabel,
    $minWidth: layoutConfig?.minWidth, // Pass minWidth to GridFormContainer
    $gap: layoutConfig?.gap, // Pass gap to GridFormContainer
    $breakpoints: layoutConfig?.breakpoints, // Pass breakpoints
  };

  return (
    <ThemeProvider theme={mergedTheme}>
      <LayoutComponent {...layoutComponentProps}>{children}</LayoutComponent>
    </ThemeProvider>
  );
};

export default FormLayout;
